<template>
  <div style="margin:20px">
    <Form ref="addForm" :model="addForm" :rules="addRule" label-position="left" :label-width="120">
      <FormItem label="店铺名称" prop="name">
        <Input v-model.trim="addForm.name" placeholder="请输入店铺名称！" size="large"></Input>
      </FormItem>
      <FormItem label="店铺地址" prop="address">
        <Input v-model.trim="addForm.address" placeholder="请输入店铺地址！" size="large"></Input>
      </FormItem>
      <FormItem label="店铺图片" prop="picture">
        <single-uploader :image='addForm.picture' @change='handleImgChange'></single-uploader>
      </FormItem>
      <FormItem label="推荐人编码" prop="referrer">
        <Input v-model.trim="addForm.referrer" placeholder="请输入推荐人编码，一旦设置不可修改！" size="large"></Input>
      </FormItem>
      <FormItem label="店铺账号" prop="managername">
        <Input v-model.trim="addForm.managername" placeholder="请设置账号(用于登录)，一旦设置不可修改！" size="large" :maxlength="12"></Input>
      </FormItem>
      <FormItem label="店主手机号" prop="telphone">
        <Input v-model.trim="addForm.telphone" placeholder="请输入店主手机号！" size="large" :maxlength="11"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" :loading="loading" @click="handleSubmit('addForm')" size="large" style="width:120px;">
          <span v-if="!loading">提交</span> <span v-else>正在保存..</span>
        </Button>
        <Button type="ghost" @click="handleReset('addForm')" size="large" style="width:120px;">取消</Button>
      </FormItem>
    </Form>
    <!--<Modal v-model="showConfirm" title="确认提交提示" @on-ok="addOk">
      <p>一旦确认提交,账号不可修改!</p>
    </Modal>-->
  </div>
</template>
<script>
  import singleImgUploader from '../common/SingleImgUploader.vue'

  export default {
    data() {
      const validateTel = (r, v, callback) => {
        let patrn = /^[0-9]*$/;
        if (!patrn.test(v)) {
          callback(new Error('手机号格式不对'));
        } else if (v.length!=11) {
          callback(new Error('手机号位数不对'));
        } else {
          callback();
        }
      };
      const validateName=(r, v, callback) => {
        let patrn = /^[0-9a-zA-Z]*$/;
        if(v.length==0){
          callback(new Error('账号不能为空'));
        }if (!patrn.test(v)) {
          callback(new Error('账号格式不对,只能是数字和字母的组合'));
        } else {
          callback();
        }
      };
      return {
        loading: false,
        addForm: {
          name: '',
          address: '',
          picture: '',
          referrer: '',
          managername: '',
          telphone: ''
        },
        addRule: {
          name: [
            {required: true, message: '请输入店铺名称', trigger: 'blur'}
          ],
          address: [
            {required: true, message: '请输入店铺地址', trigger: 'change'}
          ],
          picture: [
            {required: true, message: '请选择店铺图片', trigger: 'blur'}
          ],
          managername: [
            {validator: validateName, trigger: 'blur'},
          ],
          telphone: [
            {validator: validateTel, trigger: 'blur'}
          ]
        },
        /*showConfirm:false*/
      }
    },
    components: {
      'single-uploader': singleImgUploader,
    },
    mounted: function () {
      this.$sitemap.prvname = "店铺管理";
      this.$sitemap.currname = '添加店铺';
    },
    methods: {
      //上传图片
      handleImgChange(img) {
        this.addForm.picture = img.url
      },
      //格式化表单数据
      formatData(){
        let that=this;
        let params={};
        Object.keys(that.addForm).forEach(key=>{
            switch (key){
              case 'referrer':
                  if(that.addForm[key]==''){
                    console.log('无');
                  }else{
                    params[key]=that.addForm[key];
                  }
                  break;
              default:
                params[key]=that.addForm[key]
            }
        });
        return params;
      },
      //添加提交
      handleSubmit(name) {
        let that=this;
        this.$refs[name].validate((valid) => {
          if(valid){
            console.log(that.formatData());
            that.$http.post(that.$api.store_add, that.formatData()).then(res => {
              if(res.data.result==1){
                that.$Message.success('添加成功');
                that.$router.push('/store/list');
              }else{
                that.$Message.error(res.data.msg);
              }
            }).catch(err => {
              that.$Message.error('服务器异常');
            })
          }else{
            that.$Message.error('请核对信息');
          }
        })
      },
      handleReset(name) {
        this.$Confirm('所做的修改没有保存，请确认取消', '提示').then(res => {
          this.$router.push('/store/list')
        }).catch(error => {
        });
      },
    }
  }
</script>



